.news {
  width: 100%;
  min-height: 850px;
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  display: flex;
}

.news__title {
  text-align: center;
  color: #000;
  text-shadow: 1px 0 10px #000;
  font-family: Gelasio;
  font-size: 43px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
}

.news__subtitle {
  text-align: center;
  color: #000;
  opacity: .6;
  margin-top: 20px;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 29px;
}

.news__content {
  padding: 56px 20px;
  display: flex;
}

.news__content__block1 {
  flex-direction: column;
  margin: 0 30px;
  display: flex;
  box-shadow: 0 6px 35px #00000014;
}

.news__content__block1__title {
  color: #000;
  margin-top: 35px;
  font-family: Gelasio;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 32px;
}

.news__content__block1__subtitle {
  color: #000;
  opacity: .6;
  margin-top: 10px;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 29px;
}

.news__content__block1__link {
  color: #000;
  margin-top: 10px;
  font-family: Roboto;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px;
  text-decoration: none;
}

.news__content__block2 {
  flex-direction: column;
  display: flex;
}

.news__content__block2__content {
  margin-bottom: 30px;
  display: flex;
  box-shadow: 0 6px 35px #00000014;
}

.news__content__block2__content__text {
  margin-left: 30px;
}

@media screen and (max-width: 1200px) {
  .news__title {
    font-size: 30px;
  }

  .news__content__block2 {
    display: none;
  }

  .news__content__block1 {
    text-align: center;
  }

  .news__content__block1 img {
    width: 100%;
  }
}

